@import "../colours";

#PageWrapperContainer {
  position: relative;
  overflow: hidden;

  #topShadow {
    width: 200%;
    height: 50px;
    position: absolute;
    top: -50px;
    // box-shadow: 0px 50px 100px 20px $Colour_Gray;
    box-shadow: 0px 50px 100px 0px $Colour_Black;
    z-index: 1;
  }

  #PageWrapperContainer_Children {
    margin-left: 20px;
    margin-right: 10px;
    overflow-y: auto;
  }
}
