@import "../colours";

.PageHeaderContainer {
  display: flex;
  flex-flow: column;
  color: $Colour_White;
  border-bottom: solid 1px $Colour_LightGray;
  padding-bottom: 10px;
  margin-bottom: 10px;
  overflow: hidden;

  .Header {
    display: flex;
    flex-direction: row;
    flex-flow: row;
    align-items: center;

    ion-icon {
      width: 35px;
      height: 35px;
    }
  }
}
