@import "./colours";

#mainContainer {
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;

  background-color: $Colour_Gray;
  justify-content: center;
  align-items: center;
  // align-self: center;
  // justify-self: center;
  box-shadow: inset 0 0 50px 5px black;

  #leftNav {
    min-height: 28rem;
    min-width: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    // justify-content: center;

    background-color: $Colour_Gray;
    box-shadow: inset 0px 0px 40px 5px rgba(0, 0, 0, 0.5);
  }

  #centerPerson {
    height: 90vh; //850px; //90vh;
    width: 23rem;
    min-width: 15rem;
    z-index: 1;
  }

  #rightPages {
    height: 45rem;
    width: 50rem;
    min-width: 20rem;
    background-color: $Colour_Gray;
    box-shadow: inset 0px 0px 100px 50px rgba(0, 0, 0, 0.5);
    overflow-y: auto;
  }
}
