@import "../colours";

//PROJECT IMAGES DIMENSIONS
// $originalWidht: 750px * $scaleMultiplier;
// $originalHeight: 1334px * $scaleMultiplier;

$scaleMultiplier: 0.3;

//FRAME DIMENSIONS
$phoneFrameWidht: 826px * $scaleMultiplier;
$phoneFrameHeight: 1718px * $scaleMultiplier;

$paddingTop: 192px * $scaleMultiplier;
$paddingLef: 38px * $scaleMultiplier;
$paddingRight: 38px * $scaleMultiplier;
$paddingBottom: 191px * $scaleMultiplier;

//CONTROLS
$controlPanelHeight: 150px * $scaleMultiplier;
$buttonSize: 120px * $scaleMultiplier;

.ProjectDisplay_Container {
  // background-color: red;
  display: flex;
  position: relative;
  // min-width: $phoneFrameWidht;
  // height: $phoneFrameHeight;
  // background-image: url("/phoneframe.png");
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  margin: 20px;
  // box-sizing: border-box;

  .ProjectHeader {
    position: absolute;
    // display: flex;
    width: 100%;
    // top: 15px;
    text-align: center;
    color: $Colour_White;
  }

  .LoadingText {
    align-self: center;
    text-align: center;
    width: 100%;
    color: $Colour_White;
  }

  .ProjectDisplay_Background {
    position: absolute;
    // top: $paddingTop;
    // left: $paddingLef;
    // bottom: $paddingBottom;
    // right: $paddingRight;

    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    pointer-events: none;

    transition: all 0.3s;
  }

  .Controls {
    background-color: $Colour_Gray;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: 10px;
    left: 15px;
    right: 15px;
    border-radius: 20px;
    padding: 2px;
    // padding-right: 3px;
    // padding-left: 3px;

    // height: $controlPanelHeight;

    .Controls_Progress {
      color: $Colour_White;
      text-align: center;
      // background-color: red;
      width: 50%;
    }

    .Controls_Button {
      // width: $buttonSize;
      // height: $buttonSize;
      color: $Colour_Secondary;
      cursor: pointer;

      ion-icon {
        width: 100%;
        height: 100%;
      }
    }
  }
}
